body {
  background: white;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: black;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.center {
  max-width: 1200px;
  width: calc(100% - 30px);
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  box-sizing: border-box;
}

@media(max-width: 990px) {
  .center {
    max-width: 720px;
  }
}

@media(max-width: 767px) {
  .center {
    max-width: none;
    margin-right: 0;
    margin-left: 0;
    width: 100%;
  }
}

*:hover,
*:focus,
*:active {
  outline: 0;
  outline-offset: 0;
}

p {
  margin: 0;
}

.wrapper1 {
  background-image: url('../img/background1.svg');
  background-position: center 45%;
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.wrapper2 {
  background-image: url('../img/background3.svg');
  background-position: center 50px;
  background-repeat: no-repeat;
  background-size: 100% auto;
}

@media(max-width: 990px) {
  .wrapper1 {
    background-image: url('../img/background1-tablet.svg');
    background-position: center 45%;
    background-repeat: no-repeat;
    background-size: 100% auto;
  }
  
  .wrapper2 {
    background-image: url('../img/background3-tablet.svg');
    background-position: center 50px;
    background-repeat: no-repeat;
    background-size: 100% auto;
  }
}

@media(max-width: 767px) {
  .wrapper1 {
    background-image: url('../img/background1-mob.svg');
    background-position: center 45%;
    background-repeat: no-repeat;
    background-size: 100% auto;
  }
  
  .wrapper2 {
    background-image: url('../img/background3-mob.svg');
    background-position: center 50px;
    background-repeat: no-repeat;
    background-size: 100% auto;
  }
}

.valid-error {
  color: red;
  font-size: 11px;
}


.loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0,0,0,.3);
  z-index: -99;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;

  &::before {
    content: url(../img/loader.svg);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &.active {
    z-index: 99;
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
}

