.footer {
  position: relative;
  height: 70px;
  display: none;

  &.active {
    display: block;
  }
}

.footer_wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}

.footer_button {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transition: .5s;
  transform: translateX(-50%);
  z-index: 999;

  &:hover {
    cursor: pointer;
  }
}

.footer_copyright {
  bottom: -70px;
  transition: .5s;
  font-size: 14px;
  position: relative;
  text-decoration: underline;
  color: black;
  display: inline;

  &:hover {
    cursor: pointer;
  }
}

.footer.open .footer_button {
  bottom: 50px;
  transform: translateX(-50%) rotate(180deg);
}

.footer.open .footer_copyright {
  bottom: 10px;
  transition: .5s;
}

.footer-information {
  font-family: 'Ubuntu', sans-serif;
  font-size: 14px;
  line-height: 120%;
  padding-top: 10px;
  padding-bottom: 15px;
  display: none;
  flex-direction: column;
  
  & p {
    margin-bottom: 15px;
  }

  & a {
    color: black;
  }

  &.open {
    display: flex;
  }
}

.footer_link {
  margin-bottom: 15px;
}

.footer_developer {
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

.footer_logo {
  margin-left: 4px;
}

@media(max-width: 767px) {
  .footer-information {
    font-size: 14px;
  }
}