.quiz {
  width: 100%;
  height: 100%;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  padding-top: 100px;
  padding-bottom: 100px;

  &.active {
    display: flex;
  }
}

.question {
  background: #F6F6F6;
  border-radius: 10px;
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 45px 92px;
  max-width: 600px;
  width: 100%;
  box-sizing: border-box;
  margin-right: 15px;
  margin-left: 15px;
  flex-shrink: 0;

  &.active {
    display: flex;
  }
}

.question.result {
  padding: 45px 35px;
}

.question_steps {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-bottom: 12px;
}

.question_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 120%;
  color: #000000;
  display: inline-block;
  text-align: center;
}

.question_text {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #11244B;
  margin-top: 10px;
}

.question_body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  margin-top: 34px;
}

.result .question_body {
  flex-direction: column;
  align-items: center;
  padding-left: calc(100px - 35px);
  padding-right: calc(100px - 35px);
}

.question_checkbox,
.multipleChoiceOrAll input[type="checkbox"] {
  display: none;
}

.question_checkbox:checked + .question_answer,
.multipleChoiceOrAll input[type="checkbox"]:checked + .question_answer {
  background: #4A50ED;
  color: white;
}

.question_answer {
  flex-basis: calc(50% - 8px);
  flex-grow: 0;
  flex-shrink: 0;
  flex-shrink: 0;
  background: #E7E7E7;
  border-radius: 5px;
  margin-bottom: 16px;
  box-sizing: border-box;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: 'Ubuntu', sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 120%;
  min-height: 54px;

  &:hover {
    cursor: pointer;
  }

  &:nth-of-type(odd) {
    margin-right: 16px;
  }
}

.question_text-input {
  background: #E7E7E7;
  border: none;
  border-radius: 5px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  color: black;
  width: 100%;
  padding: 17px 20px;
  margin-bottom: 10px;
  box-sizing: border-box;
  width: 400px;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    appearance: none;
    margin: 0;
  }

  &[type=number] {
    -moz-appearance: textfield;
  }

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat, #E7E7E7;
  }

  &.invalid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 7L7 17' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M17 17L7 7' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") 98% center no-repeat, #E7E7E7;
  }

  &::placeholder {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 120%;
    color: #939393;
  }
}

.question_prompt {
  font-size: 15px;
  color: #929292;
}

.question_attention {
  display: inline-block;
  width: 100%;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-top: 8px;
  margin-bottom: 18px;
}

.question_protection {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120%;
  color: #BD4D4E;
  margin-bottom: 17px;
  display: flex;
  flex-direction: row;
  align-items: center;

  & svg {
    margin-right: 12px;
  }
}

#accepting {
  display: none;
}

#accepting:checked ~ .question_accepting::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='12' height='9' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.600586 3.9999L5.40059 7.5999L11.4006 0.399902' stroke='%232B4FA9'/%3E%3C/svg%3E%0A") center no-repeat;
}

.question_accepting {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120%;
  text-align: center;
  color: #2B4FA9;
  position: relative;
  display: inline-block;
  padding-left: calc(18px + 8px);

  &:hover {
    cursor: pointer;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 18px;
    height: 18px;
    border: 1px solid #2B4FA9;
    background: none;
    position: absolute;
    top: calc(50% - 9px);
    left: 0;
    box-sizing: border-box;
  }

  & a {
    color: #2B4FA9;
  }
}

.question_pagination {
  margin-top: 34px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  box-sizing: border-box;

  &--after-checkbox {
    margin-top: calc(34px - 16px);
  }
}

.result .question_pagination {
  margin-top: 26px;
  justify-content: center;
}

.first-question .question_pagination {
  justify-content: flex-end;
}

.question_next {
  background: #4A50ED;
  box-shadow: 0px 0px 40px rgba(74, 80, 237, 0.4);
  border-radius: 5px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 17px 0;
  flex-basis: 160px;
  border: none;

  &.submit {
    text-transform: none;
  }

  &.disabled {
    opacity: 0.3;
    pointer-events: none;

    &:hover {
      cursor: auto;
    }
  }

  &:hover {
    cursor: pointer;
  }

  &.submit {
    flex-basis: 280px;
    font-size: 18px;
    font-weight: 700;
    background: #15AF40;
    box-shadow: 0px 0px 50px rgba(21, 175, 64, 0.6);
  }
}

.question_prev {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 120%;
  color: #2B4FA9;
  margin-right: 16px;
  text-align: center;
  box-sizing: border-box;
  text-decoration: underline;

  &:hover {
    cursor: pointer;
  }
}

.valid-error {
  color: #CB3D54;
  font-size: 11px;
  line-height: 120%;
  text-align: center;
  width: 100%;
  display: inline-block;
}

@media(max-width: 767px) {
  .form {
    justify-content: flex-start;
  }

  .quiz {
    padding-top:40px;
    padding-bottom: 40px;
  }

  .question {
    padding: 24px 20px;
    width: calc(100% - 30px);
  }

  .question.result {
    padding: 24px 20px;
  }

  .question_steps {
    font-size: 14px;
    line-height: 120%;
    margin-bottom: 8px;
  }

  .question_title {
    font-size: 18px;
    line-height: 120%;
  }

  .question_text {
    font-size: 13px;
  }

  .question_body {
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    margin-top: 24px;
  }

  .result .question_body {
    padding: 0;
  }

  .question_answer {
    flex-basis: auto;
    width: 100%;
    margin-bottom: 10px;

    &:nth-of-type(odd) {
      margin-right: 0;
    }
  }

  .question_pagination {
    margin-top: 14px;
  }

  .result .question_pagination {
    margin-top: 19px;
  }

  .question_next {
    flex-basis: 150px;
    font-size: 16px;

    &.submit {
      font-size: 18px;
    }
  }

  .result .question_next {
    width: 100%;
    box-sizing: border-box;
    flex-grow: 1;
  }

  .first-question .question_next {
    font-size: 18px;
    flex-basis: 170px;
  }

  .question_prev {
    font-size: 14px;
  }

  .question_text-input {
    margin-bottom: 10px;
    width: 100%;
    font-size: 18px;

    &::placeholder {
      font-size: 18px;
    }
  }

  .question_attention {
    font-size: 11px;
    margin-top: 6px;
    margin-bottom: 19px;
  }

  .question_accepting {
    font-size: 12px;

    &::before {
      position: relative;
      margin-right: 10px;
      top: 13px;
      width: 14px;
      height: 14px;
    }
  }

  #accepting:checked ~ .question_accepting::before {
    background: url("data:image/svg+xml,%0A%3Csvg width='8.4' height='5.6' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.600586 3.9999L5.40059 7.5999L11.4006 0.399902' stroke='%232B4FA9'/%3E%3C/svg%3E%0A") center no-repeat;
  }

  .question_protection {
    margin-bottom: 12px;
  }
}
